import React, { Component } from 'react';
import User from "../../images/user.png"
import Background from "../../images/background.jpg";
import ebe from "../../images/testimony/badge.png";
import { Link } from "gatsby";
import "material-icons/iconfont/material-icons.css";
import "materialize-css/dist/css/materialize.min.css";
import { getFirebase } from '../firebase.js';


class Signin extends Component {

    constructor() {
        super()
        this.state = {
            email: "",
            password: ""
        }
    }

    auth;
    M;

    componentDidMount() {
        // const lazyFirebase = import("firebase/app");
        // const lazyAuthentication = import("firebase/firebase-auth");
        // Promise.all([lazyFirebase, lazyAuthentication])
        //     .then(([firebase]) => {
        //         console.log(firebase)
        //         this.auth = getFirebase(firebase).auth();
        //         //this.auth = auth;
        //         this.auth.onAuthStateChanged(user => {
        //             const loggedOutContent = document.querySelectorAll(".logged-out");
        //             const loggedInContent = document.querySelectorAll(".logged-in");
        //             if (user) {
        //                 loggedInContent.forEach(content => {
        //                     content.style.display = "block"
        //                 })
        //                 loggedOutContent.forEach(content => {
        //                     content.style.display = "none"
        //                 })
        //                 document.getElementById("signed-in-email").innerHTML = user.email;
        //             } else {
        //                 loggedOutContent.forEach(content => {
        //                     content.style.display = "block"
        //                 })
        //                 loggedInContent.forEach(content => {
        //                     content.style.display = "none"
        //                 })
        //             }
        //         })
        //     })
        const M = require("materialize-css/dist/js/materialize.min.js");
        this.M = M;
        var elems = document.querySelectorAll(".sidenav");
        var signModal = document.querySelectorAll(".modal");
        M.Sidenav.init(elems);
        M.Modal.init(signModal);
    }

    changeHandler(e) {
        const target = e.target;
        const name = target.name;
        this.setState({ [name]: target.value })
    }

    submitHandler = (e) => {
        const { email, password } = this.state;
        e.preventDefault();
        this.auth.signInWithEmailAndPassword(email, password)
            .then(() => {
                this.M.toast({ html: `You have been succesfully signed in` })
            })
            .catch((error) => {
                this.M.toast({ html: `${error.message}` })
            })
        // this.setState({ email: "", password: "" });
    }

    render() {
        return (
            <div id="sign-in" className="modal" style={{ maxWidth: "700px" }}>
                <div className="modal-content">
                    <form onSubmit={(e) => this.submitHandler(e)}>
                        <center>
                            {/* username */}
                            <div className="row">
                                <div className="input-field col s12">
                                    <input name="email" id="email" type="email" value={this.state.email} onChange={(e) => { this.changeHandler(e) }} />
                                    <label htmlFor="header">Email</label>
                                </div>
                            </div>
                            {/* password */}
                            <div className="row">
                                <div className="input-field col s12">
                                    <input name="password" id="password" type="password" value={this.state.password} onChange={(e) => { this.changeHandler(e) }} />
                                    <label htmlFor="password">Password</label>
                                </div>
                            </div>
                            {/* submit */}
                            <div className="row">
                                <div className="input-field col s12">
                                    <button name="submit" id="submit" type="submit" value="send" className="btn waves-effect waves-light" >Sign in
                                    <i className="material-icons right">send</i>
                                    </button>
                                </div>
                            </div>
                        </center>
                    </form>
                </div>
            </div>
        );
    }

}

class Nav extends Component {
    constructor() {
        super()
        this.state = {
            email: "",
            password: ""
        }
    }

    auth;
    M;

    componentDidMount() {
        const lazyFirebase = import("firebase/app");
        const lazyAuthentication = import("firebase/firebase-auth");
        Promise.all([lazyFirebase, lazyAuthentication])
            .then(([firebase]) => {
                this.auth = getFirebase(firebase).auth();
                this.auth.onAuthStateChanged(user => {
                    const loggedOutContent = document.querySelectorAll(".logged-out");
                    const loggedInContent = document.querySelectorAll(".logged-in");
                    if (user) {
                        loggedInContent.forEach(content => {
                            content.style.display = "block"
                        })
                        loggedOutContent.forEach(content => {
                            content.style.display = "none"
                        })
                    } else {
                        loggedOutContent.forEach(content => {
                            content.style.display = "block"
                        })
                        loggedInContent.forEach(content => {
                            content.style.display = "none"
                        })
                    }
                })
            })
        const M = require("materialize-css/dist/js/materialize.min.js");
        this.M = M;
        var elems = document.querySelectorAll(".sidenav");
        var signModal = document.querySelectorAll(".modal");
        this.M.Sidenav.init(elems);
        this.M.Modal.init(signModal);
    }

    signOutHandler() {
        this.auth.signOut()
            .then(() => {
                this.M.toast({ html: `You have succesfully signed out` })
            })
            .catch((error) => {
                this.M.toast({ html: `${error.message}` })
            })
    }

    render() {
        return (
            <>
                <nav className="sm sticky">
                    <div className="nav-wrapper">
                        <a href="#!" className="brand-logo"><img src={ebe} alt="logo" width="50" height="50" /></a>
                        <a href="#!" data-target="mobile-demo" className="sidenav-trigger"><i className="material-icons">menu</i></a>
                        <ul className="right hide-on-med-and-down">
                            <li><Link to="/">Home</Link></li>
                            <li><Link to="/contact us">Contact Us</Link></li>
                            <li><Link to="/gallery">Gallery</Link></li>
                            <li className="logged-out" style={{ display: "none" }}><a className="modal-trigger" href="#sign-in" >Sign in</a></li>
                            <li className="logged-in" style={{ display: "none" }}><Link to="/account">Account</Link></li>
                            <li className="logged-in" style={{ display: "none" }} onClick={this.signOutHandler}><Link to="/">Sign out</Link></li>
                            <li className="logged-in" style={{ display: "none" }}><Link to="/cbt">C B T</Link></li>
                        </ul>
                    </div>
                </nav>
                <ul className="sidenav sticky" id="mobile-demo">
                    <li>
                        <div className="user-view">
                            <div className="background">
                                <img src={Background} alt="bg" />
                            </div>

                            <a href="#!"><img className="circle" src={User} alt="" /></a>
                            <a href="#!"><span className="name white-text">Hello</span></a>
                            <a href="#!"><span className="email white-text" id="signed-in-email"></span></a>
                        </div>
                    </li>
                    <li><Link to="/"><i className="material-icons red-text" style={{ marginRight: "5px" }}>home</i>Home</Link></li>
                    <li><Link to="/contact us"><i className="material-icons red-text" style={{ marginRight: "5px" }}>phone</i>Contact Us</Link></li>
                    <li><Link to="/gallery"><i className="material-icons red-text" style={{ marginRight: "5px" }}>image</i>Gallery</Link></li>
                    <li className="logged-out" style={{ display: "none" }}><a className="modal-trigger" href="#sign-in"><i className="material-icons red-text" style={{ marginRight: "5px" }}>person</i>Sign in</a></li>
                    <li className="logged-in" style={{ display: "none" }}><Link to="/account"><i className="material-icons red-text" style={{ marginRight: "5px" }}>person</i>Account</Link></li>
                    <li className="logged-in" style={{ display: "none" }} onClick={this.signOutHandler}><Link to="/"><i className="material-icons red-text" style={{ marginRight: "5px" }}>exit_to_app</i>Sign out</Link></li>
                    <li className="logged-in" style={{ display: "none" }}><Link to="/cbt"><i className="material-icons red-text" style={{ marginRight: "5px" }}>computer</i>C B T</Link></li>
                </ul>
                <Signin />
            </>
        );
    }
}

export default Nav;